import { Header } from "../Header/Header"
import styles from './Settings.module.css'
import Select from 'react-select'
import { auth } from '../Auth/Auth'
import { useEffect,useState } from "react"
import { useNavigate } from "react-router-dom"


export const Settings = () =>{ 
        const options =[
            { value:'1-1',label:'1-1'},
            { value:'1-2',label:'1-2'},
            { value:'1-3',label:'1-3'},
            { value:'1-4',label:'1-4'},
            { value:'1-5',label:'1-5'},
            { value:'1-6',label:'1-6'},
            { value:'2-1',label:'2-1'},
            { value:'2-2',label:'2-2'},
            { value:'2-3',label:'2-3'},
            { value:'2-4',label:'2-4'},
            { value:'2年理探',label:'2年理探'},
            { value:'2年国探',label:'2年国探'},
            { value:'3-1',label:'3-1'},
            { value:'3-2',label:'3-2'},
            { value:'3-3',label:'3-3'},
            { value:'3-4',label:'3-4'},
            { value:'3年理探',label:'3年理探'},
            { value:'3年国探',label:'3年国探'},
        ]
    const handleChange = selectedOption => {
        localStorage.setItem('class',selectedOption.value)
        setclasses(selectedOption.value)
    };
    
    const [classes,setclasses] =useState(localStorage.getItem('class'))
    useEffect(()=>{
        console.log(classes)
    },[classes])
    const navigate = useNavigate()
    useEffect(()=>{
    if (!(auth.currentUser)){
        navigate('/',{state:'/Settings'})
    }})
    if(auth.currentUser){
        return(
            <div>
        <Header />
        <p className={styles.greeting}>Hello, {auth.currentUser.displayName}</p> <br />
        <div className={styles.info}>
        <p>Your Address : {auth.currentUser.email}</p> <br />
        <p>Class : {classes}</p>
        </div>
        <Select options={options} onChange={handleChange} />
        <div>
            <button type="submit"></button>
    </div>
    <button onClick={() =>{console.log(auth)}}>check</button><br />
</div>
)
}
}


